import React, { useEffect } from 'react';
import { useForm } from 'react-hook-form';
import { useSelector } from 'react-redux';
import { useHistory } from 'react-router';
import { InputGroupInline, WindowComponent, WindowComponentProps } from 'summer';
import { hasEmpresa } from '../../../../../../dependencies/empresaDependency';
import { hasEmpresaMLF } from '../../../../../../dependencies/empresaMLFDependency';
import { getDefaultPrinterDataBasic, PrinterDataBasic } from '../../../../../../models/dtos/ger/printerDataBasic';
import { DATES } from '../../../../../../models/enumerated/ger/printerConfigTypeEnum';
import { Reducers } from '../../../../../../store/ducks';
import { GlobalState } from '../../../../../../store/ducks/global.duck';
import ReportFormView from '../../../../report/ReportFormView';
import { CLASS_KEY, RelacaoEntradaSaidaRV } from '../relacaoEntradaSaidaRV';

export const REPORT_EVENT: string = 'printDemonstrativoEntradaICMSDA';
export const REPORT_TITLE = 'Demonstrativo de Entradas com Diferença de Alíquota';

const BASIC_DATA: PrinterDataBasic = {
  ...getDefaultPrinterDataBasic(REPORT_TITLE, REPORT_EVENT, CLASS_KEY, DATES.value),
  dtFinal: '31/12/2017',
  dtInicial: '01/12/2017',
};

export default function DemonstrativoEntradaICMSDAReportView(props: WindowComponentProps) {
  const history = useHistory();

  const formProps = useForm<RelacaoEntradaSaidaRV>({ mode: 'onBlur' });
  const globalReducer = useSelector<Reducers, GlobalState>(state => state.globalReducer);

  useEffect(
    () => {
      if (globalReducer.globalParameter.empresa !== null) {
        const id = globalReducer.globalParameter.empresa.id;
        setTimeout(() => {
          formProps.setValue('idGerEmpresa', id);
        }, 800);
      }
    }, // eslint-disable-next-line react-hooks/exhaustive-deps
    []
  );

  return (
    <WindowComponent
      {...props}
      title={REPORT_TITLE}
      useGlobalParameter={true}
      globalParameter={globalReducer.globalParameter}
      dependencies={[() => hasEmpresa(globalReducer.globalParameter), () => hasEmpresaMLF(globalReducer.globalParameter)]}
    >
      <ReportFormView formProps={formProps} printerData={BASIC_DATA} history={history} extraConfigOpened={true}>
        <InputGroupInline formProps={formProps} id="idGerEmpresa" hidden={true} />
      </ReportFormView>
    </WindowComponent>
  );
}
